import React, { Component } from 'react'
import {ToggleButtonGroup, ToggleButton, Nav, Container, Row, Col} from 'react-bootstrap';

import Image from './Images/me.png'

export class Title extends Component {

    componentDidMount(){
       
    }

    personalMachineClick(){
        window.location.href= "http://" + this.props.machineLinks[0]
    }

    cloudClick(){
       window.location.href=this.props.machineLinks[1]
       
    }

    render() {
        return (
            <Container>
                 <Row>
                    <Col sm={5}>
                        <Nav>
                            <Nav.Item>
                                <Container>
                                    <Row>
                                        <Col>
                                            <p>
                                                Marco Fernandes  
                                                <br/>
                                                Portfolio
                                                <br/>
                                                V2
                                            </p>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col>
                                            <div className="portfolio-me-img-container portfolio-me-img-parent">
                                                <img className="portfolio-me-img" src={Image}></img>
                                            </div>
                                        </Col>
                                    </Row>
                                </Container>
                                
                            </Nav.Item>
                        </Nav>
                    </Col>
                    <Col sm={7}>
                        <span>
                            <ToggleButtonGroup  type="radio" name="options" defaultValue={this.props.defaultMachine}>
                                <ToggleButton onClick={()=>this.cloudClick()} value="cloud" variant="secondary" >
                                    <div className="cloud-vertical-align-text-container">
                                        <span className="cloud-vertical-align-text">
                                            Cloud
                                        </span>
                                    </div>
                                </ToggleButton>
                                <ToggleButton
                                 onClick={()=>this.personalMachineClick()}
                                 value="machine"
                                 variant="secondary">
                                    Person 
                                    <br></br>
                                    Machine
                                </ToggleButton>
                            </ToggleButtonGroup>
                        </span>
                    </Col>
                 </Row>
            </Container>
        )
    }
}

export default Title
