import React, { Component } from 'react';
import {Button, Accordion, Card, Col, Row, Container} from 'react-bootstrap';

export class ProjectItem extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }

        this.cloudOpen = this.cloudOpen.bind(this);
        this.machineOpen = this.machineOpen.bind(this);
    }
    
    
    cloudOpen(e){
        e.stopPropagation();
        window.location.href= this.props.project.cloudLink;
    }

    machineOpen(e){
        e.stopPropagation();
        window.location.href= "http://" + this.props.project.machineLink;
    }

    renderMachineButton(){
        if(this.props.project.machineLink != "#0"){
            return(
                <Button onClick={(e)=>{ this.machineOpen(e)}} variant="dark">
                    <div>
                        Personal
                        <br></br>
                        Machine
                    </div>
                </Button>
            )
        }else{
            return(
                <div>
                    Machine
                    <br></br>
                    in the Works
                </div>
            )
        }
    }

    renderCloudButton(){
        if(this.props.project.machineLink != "#0"){
            return(
                <Button className="cloud-button-centered"
                    onClick={(e)=>{ this.cloudOpen(e) }}
                    variant="dark">
                    <div>
                        Cloud
                    </div>
                </Button>
            )
        }else{
            return(
                <div >
                    Cloud
                    <br></br>
                    in the Works
                </div>
            )
        }
        
    }

    render() {
        return (
            
                <Accordion>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                <Container>
                                    <Row>
                                        <Col sm={8}>
                                            <div className="project-name-container">
                                                <Button className="project-name">
                                                    <div>
                                                        {this.props.project.name}
                                                    </div>
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col sm={4}>
                                            <div className="project-machines-buttons-container">
                                                <Container className="project-machines-buttons">
                                                    <Row>
                                                        <Col>
                                                            {
                                                                this.renderMachineButton()
                                                            }
                                                        </Col>
                                                    </Row>
                                                    <Row>
                                                        <Col>
                                                            <div className="cloud-button-centered-container">
                                                                {
                                                                    this.renderCloudButton()
                                                                }
                                                            </div>
                                                        </Col>
                                                    </Row>
                                                </Container>
                                            </div>
                                        </Col>
                                    </Row>
                                </Container>
                            </Accordion.Toggle>
                        
                        
                            <Accordion.Collapse eventKey="0">
                                <div>
                                    {this.props.project.description}
                                </div>
                            </Accordion.Collapse>
                </Accordion>
                
        )
    }
}

export default ProjectItem
