import logo from './logo.svg';
import './App.css';

import Title from './Title.js';
import ProjectsContainer from './ProjectsContainer.js';
import {Button, Container, Modal, Row, Col} from 'react-bootstrap';
import configs from './ProjectConfigs.js';

import 'bootstrap/dist/css/bootstrap.min.css';
import './BootstrapCorrections.css'


import React, { Component } from 'react'

export class App extends Component {
  
  constructor(props) {
    super(props)
  
    this.state = {
      showTechnologies : false,
      isFadeInNext : false
    }

    this.technologiesClick = this.technologiesClick.bind(this);
    this.personalMachineClick = this.personalMachineClick.bind(this);
    this.cloudClick = this.cloudClick.bind(this);
  }
  
  personalMachineClick(){
    window.location.href= "http://" + this.props.machineLinks[0]
  }

  cloudClick(){
    window.location.href=this.props.machineLinks[1]
  }

  renderTechnologies(){
    if(this.state.showTechnologies){

      return(
        <Modal.Dialog className={ (this.state.isFadeInNext ? "fade-in" : "fade-out") +" tech-modal"} >
          <Modal.Header onClick={()=>this.technologiesClick()}>
            <Modal.Title>
                  Technologies
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
              <p><b>React</b>
                <br></br>
                -react-bootstrap
                <br></br>
                <b>Personal Machine</b>
                <br></br>
                -Docker
                <br></br>
                -Nginx
              </p>
          </Modal.Body>
  
          <Modal.Footer>
            <Button onClick={()=>this.technologiesClick()} variant="secondary">
              Close
            </Button>
          </Modal.Footer>
        </Modal.Dialog>
      )
    }else{
      return(
        <div></div>
      )
    }
  
    
  }

  oldPortfolioClick(){
    window.location.href =  configs.oldPortfolio;
  }

  oldVersionClick(){
    window.location.href=configs.versions[0];
  }

  technologiesClick(){

    if(this.state.showTechnologies === true){
      var thisReference = this;
      this.setState({
        isFadeInNext : false
      });

      setTimeout( 
        function(){
          thisReference.setState({
            showTechnologies : false
          });
        }
      ,1000);
    }else{
      this.setState({
        isFadeInNext : true, 
        showTechnologies: true
      });
    }
  }

  render() {
    return (
      <div>
        
              <Title defaultMachine={configs.defaultMachine} machineLinks={configs.machineLinks}></Title>
              <ProjectsContainer configs={configs}></ProjectsContainer>
        
              <div className="footer-portfolio">
                <Button onClick={()=>this.oldVersionClick()}>V1</Button>
                <Button onClick={()=>this.oldPortfolioClick()}>Old Protfolio</Button>
                <Button onClick={()=>this.technologiesClick()}>Technologies</Button>
              </div>
       
        {
          this.renderTechnologies()
        }
    </div>
    )
  }
}

export default App

